.recip-img img{
    width: 210px;
    height: 140px;
    margin-left: 5px;
}
.recip-container{
    display:flex;
    margin-top: 2%;   
    margin-right: auto;
    justify-content: center;
}
.recip{
    margin-right: 1em;
    border: 1px solid rgba(128, 128, 128, 0.405);
    border-radius: 20px;
    padding: 10px;
    width: 220px;
 
}
.btn-3{
    width: 220px;
    margin-bottom: 20px;
    border: 1px solid gray;
    border-radius: 7px;
    padding: 15px;
    background-color: white;
    font-weight: medium;

}
.btn-3:hover{
    background-color: orange;
    color: white;
    border: none;
    font-weight: bold;
}
.popular .barre {
    background-color: white;
}
.popular .barre table {
    background-color: white;
    margin-top: -80px;
}
.popular .barre .main{
    background-color: white;
}
.popular .container{
    display: none;
}
.popular .barre .title{
    display: none;
}
.popular .close button{
    margin-left: 130px;
    margin-top: 20px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #000;
    background-color: orange;
   
}
.popular .close button:hover{
    background-color: white;
    color: black;
}