body{
    font-family: Metrika;
}
.header h1{
    font-family: Catallina;
}
.header{
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: Metrika;
}
.left{
    position: relative;
    left: 10%;
}
.right{
    position: absolute;
    margin-left: 80%;
}
.btn{
    background-color: orange;
    color: white;
    padding: 10px;
    border-radius: 10px;
    border: none;
    font-family: Metrika;
    
}
.middle{
    margin-left: auto;
    margin-right: auto;
}
.middle a {
    text-decoration: none;
    color: black;
    margin-left: 2em;
}
#home{
    color: orange;
}
.section-1{
    margin-top: 5%;
    display: flex;
}
.left p{
    font-size: 13px;
    color: gray;
    
}
.fluo{
    color: orange;
}
.btn-2{
    background-color: orange;
    color: white;
    padding: 10px;
    border-radius: 10px;
    border: none;
    font-family: Metrika;
    text-decoration: none;
}
.btn-2:hover{
    background-color: white;
    border: 1px solid #000;
    color: black;
}
.subright{
margin-left: auto;
position: relative;
right: 10%;
}
.subright img{
    width: 450px;
    height: 350px;
}