.barre{
    margin-top: 30px;
    padding-top: 10px;
    padding-left: 20px;
    padding-right: 20px;
    padding-bottom: 20px;
 
}
.barre .container{
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 20px;
    background-color: rgba(255, 166, 0, 0.267);
    display: flex;
    flex-direction: row;
}
.row input{
    border: none;
    margin-right: 2em;
    margin-left: 2em;
    background-color: transparent;
}

.vertical{
     /* Ajustez la hauteur selon vos besoins */
    height: 25px;
    border-left: 1px solid #ddd; /* Couleur de la ligne */
    margin-left: 0;

}
.btn-4{
    margin-left: auto;
    height: 100%;
}
.btn-4 button{
    padding: 7px;
    width: 150px;
    background-color: #fff;
    color: #000;
    border: 1px solid #000;
    border-radius: 15px;
}
.btn-4 button:hover{
    background-color: orange;
    color: #fff;
}
.barre .main{
    margin-top: 20px;
    background-color: rgba(255, 166, 0, 0.267);
}
table {
    width: 100%; 
    border-collapse: collapse; 
    background-color: rgba(255, 166, 0, 0.267);
}
th,td {
    border: 1px solid black; /* Ajouter une bordure pour visualiser les cellules */
    padding: 20px; /* Ajouter un espacement interne dans chaque cellule */
}
img{
    width: 200px;
    height: 200px;
    border-radius: 20px;
}
.btn-1
{
    margin-left: 10px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 5px;
    width: 100px;

}
.btn-1:hover{
    background-color: orange;

}
.barre{
    background-color: rgba(255, 166, 0, 0.267);
}
.holder{
    margin-top: -20px;
}