.type{
    background-color: rgba(255, 166, 0, 0.267);
    padding: 2px;
    margin-top: 10px;
}
.title{
    justify-content: center;
}
.title h1{
    font-family: Metrika;
}
.title h1{
    margin-top: 5%;
    margin-left: 30%;
    margin-right: auto;
}
.title p{
    color: gray;
    margin-left: 30%;
}
.card-container{
    display:flex;
    margin-top: 2%;   
    margin-right: auto;
    justify-content: center;
}
.card-container img{
    margin-right: 2em;
    border-radius: 5px;
    cursor: pointer;
}
.type .container{
    display: none;
}
.type .barre .title{
    display: none;
}
.type .close button{
    margin-left: 70px;
    margin-top: 20px;
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #000;
    background-color: orange;
   
}
.type .close button:hover{
    background-color: white;
    color: black;
}
